/* eslint-disable */

import Blockly from '../../blockly';

Blockly.Arduino['default_setup'] = function (block) {
    var code = Blockly.Arduino.statementToCode(block, 'SETUP');;
    return code;
};


Blockly.Arduino['default_loop'] = function (block) {
    var code = Blockly.Arduino.statementToCode(block, 'LOOP');;
    return code;
};