/* eslint-disable */

import Blockly from '../../blockly';

Blockly.Arduino['time_delay'] = function (block) {
  var delay = Blockly.Arduino.valueToCode(block, 'delay_value', Blockly.Arduino.ORDER_NONE) || '0';
  return 'delay(' + delay + ');\n';

};

Blockly.Arduino['time_delay_us'] = function (block) {
  var delay = Blockly.Arduino.valueToCode(block, 'delay_value', Blockly.Arduino.ORDER_NONE) || '0';
  return 'delayMicroseconds(' + delay + ');\n';
};

