/**
 * ***************************************************************************
 * BUZZER block definition
 * ***************************************************************************
 */
Blockly.Blocks['actuators_buzzer'] = {
  init: function () {
    this.appendDummyInput()
      .appendField('Buzzer')
      .appendField(new Blockly.FieldImage('img/components/buzzer_v0.svg', 40, 40, { alt: '*', flipRtl: 'FALSE' }))
      .appendField('Pin ')
      .appendField(new Blockly.FieldDropdown([['option', 'OPTIONNAME'], ['option', 'OPTIONNAME'], ['option', 'OPTIONNAME']]), 'digitalPin');
    this.appendValueInput('time_ms')
      .setCheck('Number')
      .appendField('Ms');
    this.appendValueInput('freq_hz')
      .setCheck('Number')
      .appendField('Hz');
    this.setInputsInline(true);
    this.setPreviousStatement(true, null);
    this.setNextStatement(true, null);
    this.setColour(20);
    this.setTooltip('');
    this.setHelpUrl('');
  }
};

