import Blockly from '../../blockly';

Blockly.Arduino['actuators_led'] = function(block) {
    var code = '';
    var digitalPin = block.getFieldValue('digitalPin');
    var state = block.getFieldValue('state');

    Blockly.Arduino.setups_['setup_led_' + digitalPin] = 'pinMode(' + digitalPin + ', OUTPUT);\n';
    code = 'digitalWrite(' + digitalPin + ',' + state + ' );\n'

    return code;
};

