import * as Blockly from '../../blockly/core';

Blockly.Blocks['sensor_potentiometer'] = {
    init: function() {
      this.appendDummyInput()
          .appendField("Potentiometer")
          .appendField(new Blockly.FieldImage("img/components/potenciometro_v0.svg", 40, 40, { alt: "*", flipRtl: "FALSE" }))
          .appendField("Pin")
          .appendField(new Blockly.FieldDropdown([["A0","A0"], ["A1","A1"], ["A2","A2"], ["A3","A3"], ["A4","A4"], ["A5","A5"]]), "analogPin")
          .appendField(new Blockly.FieldDropdown([["%","percentage"], ["0..1023","analogic"]]), "format");
      this.setOutput(true, null);
      this.setColour(210);
   this.setTooltip("");
   this.setHelpUrl("");
    }
  };


