import * as Blockly from '../../blockly/core';


Blockly.Blocks['lcd_init'] = {
  init: function () {
    this.appendDummyInput()
      .appendField("LCD Init")
      .appendField(new Blockly.FieldImage("img/components/lcd.svg", 90, 50, { alt: "*", flipRtl: "FALSE" }))
      .appendField(new Blockly.FieldDropdown([["2x16", "option_2x16"], ["4x20", "option_4x20"]]), "lcd_row_col")
      .appendField("I2C address")
      .appendField(new Blockly.FieldDropdown([["0x20", "address_0x20"], ["0x21", "address_0x21"], ["0x22", "address_0x22"]]), "i2c_address") ;
    this.setInputsInline(true);
    this.setPreviousStatement(true, null);
    this.setNextStatement(true, null);
    this.setColour(120);
    this.setTooltip("");
    this.setHelpUrl("");
  }
};

Blockly.Blocks['lcd_clear'] = {
  init: function () {
    this.appendDummyInput()
      .appendField("LCD Limpiar");
    this.setInputsInline(true);
    this.setPreviousStatement(true, null);
    this.setNextStatement(true, null);
    this.setColour(120);
    this.setTooltip("");
    this.setHelpUrl("");
  }
};

Blockly.Blocks['lcd_print'] = {
  init: function () {
    this.appendDummyInput()
      .appendField("LCD print")
      .appendField("column")
      .appendField(new Blockly.FieldDropdown([["1", "column_1"], ["2", "column_2"], ["3", "column_3"], ["4", "column_4"], ["5", "column_5"], ["6", "column_6"], ["7", "column_7"], ["8", "column_8"], ["9", "column_9"], ["10", "column_10"], ["11", "column_11"], ["12", "column_12"], ["13", "column_13"], ["14", "column_14"], ["15", "column_15"], ["16", "column_16"]]), "column")
      .appendField("row")
      .appendField(new Blockly.FieldDropdown([["1", "row_1"], ["2", "row_2"], ["3", "row_3"], ["4", "row_4"]]), "row");
    this.appendValueInput("column")
      .setCheck("String");
    this.setInputsInline(true);
    this.setPreviousStatement(true, null);
    this.setNextStatement(true, null);
    this.setColour(120);
    this.setTooltip("");
    this.setHelpUrl("");
  }
};

Blockly.Blocks['lcd_backlight'] = {
  init: function () {
    this.appendDummyInput()
      .appendField("LCD backlight")
      .appendField(new Blockly.FieldDropdown([["ON", "backlight_on"], ["OFF", "backlight_off"]]), "row");
    this.setInputsInline(true);
    this.setPreviousStatement(true, null);
    this.setNextStatement(true, null);
    this.setColour(120);
    this.setTooltip("");
    this.setHelpUrl("");
  }
};