import * as Blockly from '../../blockly/core';

Blockly.Blocks['bluetooth_init'] = {
    init: function() {
      this.appendDummyInput()
          .appendField(new Blockly.FieldImage("https://upload.wikimedia.org/wikipedia/commons/thumb/d/da/Bluetooth.svg/393px-Bluetooth.svg.png", 30, 30, { alt: "*", flipRtl: "FALSE" }))
          .appendField("Init")
          .appendField("Rx")
          .appendField(new Blockly.FieldDropdown([["0","D0"], ["1","D1"], ["2","D2"], ["3","D3"], ["4","D4"], ["5","D5"], ["6","D6"], ["7","D7"], ["8","D8"], ["9","D9"], ["10","D10"], ["11","D11"], ["12","D12"], ["13","D13"], ["A0","A0"], ["A1","A1"], ["A2","A2"], ["A3","A3"], ["A4","A4"], ["A5","A5"]]), "rx_pin")
          .appendField("Tx")
          .appendField(new Blockly.FieldDropdown([["0","D0"], ["1","D1"], ["2","D2"], ["3","D3"], ["4","D4"], ["5","D5"], ["6","D6"], ["7","D7"], ["8","D8"], ["9","D9"], ["10","D10"], ["11","D11"], ["12","D12"], ["13","D13"], ["A0","A0"], ["A1","A1"], ["A2","A2"], ["A3","A3"], ["A4","A4"], ["A5","A5"]]), "tx_pin")
          .appendField(new Blockly.FieldDropdown([["2400","bd_2400"], ["4800","bd_4800"], ["9600","bd_9600"], ["19200","bd_19200"], ["38400","bd_38400"], ["57600","bd_57600"], ["115200","bd_115200"]]), "baudRate");
      this.setPreviousStatement(true, null);
      this.setNextStatement(true, null);
      this.setColour(210);
   this.setTooltip("");
   this.setHelpUrl("");
    }
  };


  
  Blockly.Blocks['bluetooth_setTimeout'] = {
    init: function() {
      this.appendValueInput("timeOut")
          .setCheck(null)
          .appendField(new Blockly.FieldImage("https://upload.wikimedia.org/wikipedia/commons/thumb/d/da/Bluetooth.svg/393px-Bluetooth.svg.png", 25, 25, { alt: "*", flipRtl: "FALSE" }))
          .appendField("Timeout");
      this.setInputsInline(true);
      this.setPreviousStatement(true, null);
      this.setNextStatement(true, null);
      this.setColour(210);
   this.setTooltip("");
   this.setHelpUrl("");
    }
  };

