      this.appendDummyInput()
          .appendField(new Blockly.FieldImage("https://upload.wikimedia.org/wikipedia/commons/thumb/d/da/Bluetooth.svg/393px-Bluetooth.svg.png", 25, 25, { alt: "*", flipRtl: "FALSE" }))
          .appendField("Receive byte");
