import * as Blockly from '../../blockly/core';

Blockly.Blocks['motor_servo'] = {
    init: function() {
      this.appendDummyInput()
          .appendField("Servo")
          .appendField(new Blockly.FieldImage("img/components/servo_v0.svg", 40, 40, { alt: "*", flipRtl: "FALSE" }))
          .appendField(new Blockly.FieldDropdown([["ON","backlight_on"], ["OFF","backlight_off"]]), "row");
      this.appendValueInput("grades")
          .setCheck(null)
          .appendField("Grades");
      this.appendValueInput("delay")
          .setCheck(null)
          .appendField("Delay (ms) ");
      this.setInputsInline(true);
      this.setPreviousStatement(true, null);
      this.setNextStatement(true, null);
      this.setColour(290);
   this.setTooltip("");
   this.setHelpUrl("");
    }
  };