import * as Blockly from '../../blockly/core';


Blockly.Blocks['inOut_readDigital'] = {
    init: function() {
      this.appendValueInput("NAME")
          .setCheck(null)
          .appendField("Read digital Pin")
          .appendField(new Blockly.FieldDropdown([["0","D0"], ["1","D1"], ["2","D2"], ["3","D3"], ["4","D4"], ["5","D5"], ["6","D6"], ["7","D7"], ["8","D8"], ["9","D9"], ["10","D10"], ["11","D11"], ["12","D12"], ["13","D13"], ["A0","A0"], ["A1","A1"], ["A2","A2"], ["A3","A3"], ["A4","A4"], ["A5","A5"]]), "digitalPins");
      this.setOutput(true, null);
      this.setColour(230);
   this.setTooltip("");
   this.setHelpUrl("");
    }
  };

  Blockly.Blocks['inOut_writeDigital'] = {
    init: function() {
      this.appendDummyInput()
          .appendField("Write digital pin")
          .appendField(new Blockly.FieldDropdown(
            [["0","D0"], ["1","D1"], ["2","D2"], ["3","D3"], ["4","D4"], ["5","D5"], ["6","D6"], ["7","D7"], ["8","D8"], ["9","D9"], ["10","D10"], ["11","D11"], ["12","D12"], ["13","D13"], ["A0","A0"], ["A1","A1"], ["A2","A2"], ["A3","A3"], ["A4","A4"], ["A5","A5"]]), "digitalPins")          
          .appendField(new Blockly.FieldDropdown([["ON","high"], ["OFF","low"]]), "NAME");
      this.setPreviousStatement(true, null);
      this.setNextStatement(true, null);
      this.setColour(230);
   this.setTooltip("");
   this.setHelpUrl("");
    }
  };

  Blockly.Blocks['inOut_readAnalog'] = {
    init: function() {
      this.appendValueInput("NAME")
          .setCheck(null)
          .appendField("Read analog Pin")
          .appendField(new Blockly.FieldDropdown([["A0","A0"], ["A1","A1"], ["A2","A2"], ["A3","A3"], ["A4","A4"], ["A5","A5"]]), "analogPins");
      this.setOutput(true, null);
      this.setColour(230);
   this.setTooltip("");
   this.setHelpUrl("");
    }
  };

  Blockly.Blocks['inOut_writeAnalog'] = {
    init: function() {
      this.appendDummyInput()
          .appendField("Write analog pin (PWM)")
          .appendField(new Blockly.FieldDropdown([["3","D3"], ["5","D5"], ["6","D6"], ["9","D9"], ["10","D10"], ["11","D11"]]), "analogPins")          .appendField(new Blockly.FieldDropdown([["ON","high"], ["OFF","low"]]), "NAME");
      this.setPreviousStatement(true, null);
      this.setNextStatement(true, null);
      this.setColour(230);
   this.setTooltip("");
   this.setHelpUrl("");
    }
  };