import Blockly from '../../blockly';

Blockly.Arduino['sensor_potentiometer'] = function (block) {

  var analog_pin = this.getFieldValue('analogPin');
  var code = 'analogRead(' + analog_pin + ')';

  return [code, Blockly.Arduino.ORDER_NONE];
};

