/* eslint-disable */

import * as Blockly from '../../blockly/core';

Blockly.Blocks['math_integer'] = {
    init: function () {
        this.appendValueInput("number")
            .setCheck(null)
            .appendField("Integer number")
        this.setOutput(true, null);
        this.setColour(230);
        this.setTooltip("");
        this.setHelpUrl("");
        this.setInputsInline("true");
    }
};


Blockly.Blocks['math_angle'] = {
    init: function () {
        this.appendDummyInput()
            .appendField("Angle")
            .appendField(new Blockly.FieldAngle(90), "NAME");
        this.setOutput(true, null);
        this.setColour(230);
        this.setTooltip("");
        this.setHelpUrl("");
    }
};

Blockly.Blocks['math_operation'] = {
    init: function () {
        this.appendValueInput("operation")
            .setCheck(null)
            .appendField(new Blockly.FieldDropdown([["square root", "sqroot"],
            ["absolute", "absolute"],
            ["-", "negative"],
            ["log(e)", "neplog"],
            ["log(10)", "log10"],
            ["round up", "roundup"],
            ["round down", "rounddown"],
            ["sin", "senoidal"],
            ["cos", "cosenoidal"],
            ["tan", "tangent"]]), "NAME");
        this.setOutput(true, null);
        this.setColour(230);
        this.setTooltip("");
        this.setHelpUrl("");
    }
};

Blockly.Blocks['math_map'] = {
    init: function () {
        this.appendValueInput("NAME")
            .setCheck(null)
            .appendField("mapping ")
            .appendField(new Blockly.FieldVariable(null), "input")
            .appendField("from")
            .appendField(new Blockly.FieldVariable("item"), "bottom_in")
            .appendField("-")
            .appendField(new Blockly.FieldVariable("item"), "top_in")
            .appendField("to")
            .appendField(new Blockly.FieldVariable("item"), "bottom_out")
            .appendField("-")
            .appendField(new Blockly.FieldVariable("item"), "top_out");
        this.setOutput(true, null);
        this.setColour(230);
        this.setTooltip("");
        this.setHelpUrl("");
        this.setInputsInline("true");

    }
};


Blockly.Blocks['math_random'] = {
    init: function () {
        this.appendValueInput("num1")
            .setCheck(null)
            .appendField("Random Integer")
            .appendField("from");

        this.appendValueInput("num2")
            .setCheck(null)
            .appendField("to");

        this.setOutput(true, null);
        this.setColour(230);
        this.setTooltip("");
        this.setHelpUrl("");
        this.setInputsInline("true");
    }
};