<template>
  <v-container
    id='blocklyCanvas'
  >
    <BlocklyComponent
      id='blockly'
      :options='options'
      ref='foo'
    ></BlocklyComponent>

  <v-navigation-drawer
      :value='$store.getters.codeShelf'
      mobile-breakpoint='9999999'
      class='elevation-0'
      absolue
      right
      clipped
      app
      width ='35%'
      background-color='beige'
      @input = 'toggleCodeShelf($event)'
    >
      <p id='code'>
        <pre v-html='code'></pre>
      </p>
    </v-navigation-drawer>

    <xml id='startBlocks' style='display: none'>
      <block type='default_setup' inline='false' x='20' y='20' deletable='false'> </block>
      <block type='default_loop' inline='false' x='20' y='80' deletable='false'> </block>
    </xml>

  </v-container>
</template>

<script>
import { mapMutations, mapGetters } from 'vuex';
import Blockly from '../blockly';
// import DarkTheme from '../blockly/plugins/theme-dark/src/index';
import BlocklyComponent from '../components/blockly/BlocklyComponent.vue';
import BlocklyStorage from '../blockly/appengine/storage';
import '../blockly/generators/arduino';
// Block definition for each blok
import '../hello-blocks/actuator/block-definition';
import '../hello-blocks/bluetooth/block-definition';
import '../hello-blocks/motor/block-definition';
import '../hello-blocks/sensors/block-definition';
import '../hello-blocks/serialPort/block-definition';
import '../hello-blocks/text/block-definition';
import '../hello-blocks/math/block-definition';
import '../hello-blocks/inputOutput/block-definition';
import '../hello-blocks/lcd/block-definition';
import '../hello-blocks/logic/block-definition';
import '../hello-blocks/time/block-definition';
import '../hello-blocks/default/block-definition';
// Code generators for each block
import '../hello-blocks/actuator/code-generator';
import '../hello-blocks/bluetooth/code-generator';
import '../hello-blocks/motor/code-generator';
import '../hello-blocks/sensors/code-generator';
import '../hello-blocks/serialPort/code-generator';
import '../hello-blocks/text/code-generator';
import '../hello-blocks/math/code-generator';
import '../hello-blocks/inputOutput/code-generator';
import '../hello-blocks/lcd/code-generator';
import '../hello-blocks/logic/code-generator';
import '../hello-blocks/time/code-generator';
import '../hello-blocks/default/code-generator';
import '../components/blockly/blocklyapi';
import '../blockly/screenshoot';

const options = {
//  theme: DarkTheme,
  toolbox: `<xml style='display: none'>
        <category name='Logic' colour='%{BKY_MATH_HUE}' >
            <block type='controls_if'></block>
            <block type='logic_compare'></block>
            <block type='logic_operation'></block>
            <block type='logic_negate'></block>
            <block type='logic_boolean'></block>
            <block type='logic_on_off'></block>
        </category>
        <category name='Loops' colour='%{BKY_LOOPS_HUE}'>
            <block type='controls_repeat_ext'>
                <value name='TIMES'>
                    <block type='math_number'>
                        <field name='NUM'>10</field>
                    </block>
                </value>
            </block>
            <block type='controls_whileUntil'></block>
            <block type='controls_for'></block>
        </category>
        <category name='Math' colour='%{BKY_MATH_HUE}'>
            <block type='math_number'>
                <field name='NUM'>123</field>
            </block>
            <block type='math_arithmetic'></block>
            <block type='math_integer'></block>
            <block type='math_angle'></block>
            <block type='math_operation'></block>
            <block type='math_map'></block>
            <block type='math_random'> </block>
          </category>   
        <category name='In/Out' colour='%{BKY_TEXTS_HUE}'>
            <block type='inOut_readDigital'></block>
            <block type='inOut_writeDigital'></block>
            <block type='inOut_readAnalog'></block>
            <block type='inOut_writeAnalog'></block>
        </category>
        <category name='Time' colour='%{BKY_TEXTS_HUE}'>
            <block type='time_delay'> 
                <value name='delay_value'>
                    <block type='math_number'>
                        <field name='NUM'>1000</field>
                    </block>
                </value>
            </block>
            <block type='time_delay_us'> 
            <value name='delay_value'>
                <block type='math_number'>
                    <field name='NUM'>1000</field>
                </block>
            </value>
        </block>
        </category>
        <category name='Serial port' colour='%{BKY_TEXTS_HUE}'>
            <block type='serial_setBaudrate'></block>            
        </category>
        <category name='Sensors' colour='%{BKY_TEXTS_HUE}'>
            <block type='sensor_potentiometer'></block>
            <block type='sensor_ldr'></block>
            <block type='sensor_dht22'>
                <field name='digitalPin'>D7</field>
            </block>
            <block type='sensor_ultrasounds'>
                <field name='trigger'>D2</field>
                <field name='echo'>D3</field>
            </block>
        </category>
        <category name='Actuators' colour='%{BKY_TEXTS_HUE}'>
            <block type='actuators_led'></block>
        </category>
        <category name='LCD screen' colour='%{BKY_TEXTS_HUE}'>
            <block type='lcd_init'></block>
            <block type='lcd_clear'></block>
            <block type='lcd_print'></block>
            <block type='lcd_backlight'></block>
        </category>
        <category name='Motor' colour='%{BKY_TEXTS_HUE}'>
            <block type='motor_servo'> </block>
        </category>
    </xml>`,
  collapse: true,
  comments: true,
  disable: false,
  maxBlocks: Infinity,
  trashcan: true,
  horizontalLayout: false,
  toolboxPosition: 'start',
  css: true,
  rtl: false,
  scrollbars: true,
  sounds: true,
  oneBasedIndex: true,
  zoom: {
    controls: false,
    wheel: true,
    startScale: 1,
    maxScale: 3,
    minScale: 0.3,
    scaleSpeed: 1.05,
  },
  media: 'media/',
  grid: {
    spacing: 15,
    length: 2,
    colour: '#a2c9d4',
    snap: true,
  },
};
export default {
  name: 'BlocklyView',
  components: {
    BlocklyComponent,
  },
  data() {
    return {
      code: '',
      options,
    };
  },
  mounted() {
    // Add a change for render the code automatically
    this.$refs.foo.workspace.addChangeListener(this.renderContent);
    this.loadBlocks();
    window.addEventListener('unload', this.auto_save_and_restore_blocks, false);
    if ('BlocklyStorage' in window) {
      BlocklyStorage.backupOnUnload(this.$refs.foo.workspace);
    }
  },
  computed: {
    ...mapGetters({ buttonExport: 'clickedSaveButton' }),
    ...mapGetters({ buttonImport: 'clickedImportButton' }),
    ...mapGetters({ buttonScreenShoot: 'clickedScreenCaptureButton' }),
    ...mapGetters({ theFile: 'importFile' }),
  },
  updated() {
    this.auto_save_and_restore_blocks();
  },
  watch: {
    buttonExport() {
      if (this.buttonExport === true) {
        this.exportCode();
      }
      this.clearSaveState();
    },
    buttonImport() {
      if (this.buttonImport === true) {
        this.importBlocksFile(this.theFile);
      }
      this.clearImportState();
    },
    buttonScreenShoot() {
      if (this.buttonScreenShoot === true) {
        Blockly.downloadScreenshot(Blockly.getMainWorkspace());
      }

      this.clearScreenShootState();
    },
  },
  methods: {
    ...mapMutations(['toggleCodeShelf', 'setCodeTab']),
    ...mapMutations(['clearSaveState']),
    ...mapMutations(['clearImportState']),
    ...mapMutations(['clearScreenShootState']),
    renderContent() {
      this.code = Blockly.Arduino.workspaceToCode(this.$refs.foo.workspace);
    },
    loadBlocks() {
      let loadOnce;
      try {
        loadOnce = window.sessionStorage.loadOnceBlocks;
      } catch (e) {
        loadOnce = null;
      }
      if (loadOnce) {
        // Language switching stores the blocks during the reload.
        delete window.sessionStorage.loadOnceBlocks;
        const xml = Blockly.Xml.textToDom(loadOnce);
        Blockly.Xml.domToWorkspace(xml, this.$refs.foo.workspace);
      } else {
        Blockly.Xml.domToWorkspace(document.getElementById('startBlocks'), this.$refs.foo.workspace);
      }
      Blockly.Events.clearPendingUndo();
    },
    persist() {
      localStorage.myBlocks = this.myBlocks;
    },
    auto_save_and_restore_blocks() {
      if (window.sessionStorage) {
        const xml = Blockly.Xml.workspaceToDom(Blockly.getMainWorkspace());
        const text = Blockly.Xml.domToText(xml);
        window.sessionStorage.loadOnceBlocks = text;
      }
    },
    exportCode() {
      const xml = Blockly.Xml.workspaceToDom(Blockly.getMainWorkspace());
      const text = Blockly.Xml.domToText(xml);
      const link = document.createElement('a');
      try {
        link.download = 'code.xml';
        link.href = `data:application/octet-stream;utf-8,${encodeURIComponent(text)}`;
        document.body.appendChild(link);
        link.click();
        link.remove();
      } catch (e) {
        window.location.href = `data:application/octet-stream;utf-8,${encodeURIComponent(text)}`;
        // eslint-disable-next-line no-alert
        alert(e);
      }
    },
    importBlocksFile(element) {
      try {
        const file = element;
        const fr = new FileReader();
        let xml;
        // eslint-disable-next-line func-names
        fr.onloadend = function () {
          xml = Blockly.Xml.textToDom(fr.result);
          Blockly.getMainWorkspace().clear();
          Blockly.Xml.domToWorkspace(xml, Blockly.getMainWorkspace());
        };

        fr.readAsText(file);
      } catch (e) {
        // eslint-disable-next-line no-alert
        alert(e);
      }
    },
  },
};
</script>

<style>
#code {
  height: 100%;
  margin: 0;
  z-index: 1;
  padding: 48px 10px 41px;
}
#blockly {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  margin: 0px;
  padding: 48px 0px 41px;
}
</style>
