import * as Blockly from '../../blockly/core';

Blockly.Blocks['serial_setBaudrate'] = {
    init: function() {
      this.appendDummyInput()
          .setAlign(Blockly.ALIGN_CENTRE)
          .appendField(new Blockly.FieldImage("img/icons/conection.svg", 25, 25, { alt: "*", flipRtl: "FALSE" }))
          .appendField("Init Baurate")
          .appendField(new Blockly.FieldDropdown([["2400","2400"], ["4800","4800"], ["9600","9600"], ["19200","19200"], ["38400","38400"], ["57600","57600"], ["115200","115200"]]), "baudrate");
      this.setInputsInline(false);
      this.setPreviousStatement(true, null);
      this.setNextStatement(true, null);
      this.setColour(160);
   this.setTooltip("");
   this.setHelpUrl("");
    }
  };

  Blockly.Blocks['serial_setTimeout'] = {
    init: function() {
      this.appendValueInput("timeOut")
          .setCheck(null)
          .appendField(new Blockly.FieldImage("img/icons/conection.svg", 25, 25, { alt: "*", flipRtl: "FALSE" }))
          .appendField("Timeout");
      this.setInputsInline(true);
      this.setPreviousStatement(true, null);
      this.setNextStatement(true, null);
      this.setColour(160);
   this.setTooltip("");
   this.setHelpUrl("");
    }
  };

  Blockly.Blocks['serial_sendByte'] = {
    init: function() {
      this.appendValueInput("byte")
          .setCheck(null)
          .appendField(new Blockly.FieldImage("img/icons/conection.svg", 25, 25, { alt: "*", flipRtl: "FALSE" }))
          .appendField("Send byte");
      this.setInputsInline(true);
      this.setPreviousStatement(true, null);
      this.setNextStatement(true, null);
      this.setColour(160);
   this.setTooltip("");
   this.setHelpUrl("");
    }
  };

  Blockly.Blocks['serial_send'] = {
    init: function() {
      this.appendValueInput("myString")
          .setCheck("String")
          .appendField(new Blockly.FieldImage("img/icons/conection.svg", 25, 25, { alt: "*", flipRtl: "FALSE" }))
          .appendField("Send ")
          .appendField(new Blockly.FieldCheckbox("TRUE"), "newLine")
          .appendField("New line ");
      this.setInputsInline(true);
      this.setPreviousStatement(true, null);
      this.setNextStatement(true, null);
      this.setColour(160);
   this.setTooltip("");
   this.setHelpUrl("");
    }
  };

  Blockly.Blocks['serial_receiveByte'] = {
    init: function() {
      this.appendDummyInput()
          .appendField(new Blockly.FieldImage("img/icons/conection.svg", 25, 25, { alt: "*", flipRtl: "FALSE" }))
          .appendField("Receive byte");
      this.setOutput(true, null);
      this.setColour(160);
   this.setTooltip("");
   this.setHelpUrl("");
    }
  };